import React, { Component } from "react";
import renderHTML from "react-render-html";
import { siteImproveEventPush } from "./helpers/siteImproveEventPush";

//------------------- Result page ------------------------------
export class ResultPage extends Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef();
  }

  componentDidMount = () => {
    siteImproveEventPush(
      null,
      "danmark testen",
      "Load resultpage",
      `User loads resultpage for group: ${this.props.group}`
    );

    this.ref.current
      .querySelectorAll("a")
      .forEach((link) => link.addEventListener("click", this.onClick, false));
  };

  componentWillUnmount = () => {
    this.ref.current
      .querySelectorAll("a")
      .forEach((link) => link.removeEventlistner("click", this.onClick, false));
  };

  onClick = (e) => {
    const group = this.props.group;
    const text = e.target.innerText;
    siteImproveEventPush(
      null,
      "danmark testen",
      `Click ${text}`,
      `User clicked ${text} from resultpage for group: ${group}`
    );
  };

  render() {
    return (
      <div className={this.props.className} ref={this.ref}>
        <h4>Basis</h4>

        {this.props.primaryGroupName && <h2>{this.props.primaryGroupName}</h2>}

        {this.props.secondaryGroupName && (
          <h4 className="secondary-group-name">
            {this.props.secondaryGroupName}
          </h4>
        )}
        {this.props?.pris && (
          <h2 className="price">
            {this.props.pris} kr.
            <span className="price-sub-text"> pr. kvartal</span>
          </h2>
        )}
        {renderHTML(this.props.markup)}

        {(this.props.primaryButtonLink || this.props.secondaryButtonText) && (
          <div className="button-wrapper">
            {this.props.secondaryButtonLink && (
              <a href={this.props.secondaryButtonLink}>
                {this.props.secondaryButtonText}
              </a>
            )}

            {this.props.primaryButtonLink && (
              <a href={this.props.primaryButtonLink} className="primary">
                {this.props.primaryButtonText}
              </a>
            )}
          </div>
        )}
      </div>
    );
  }
}
